    getNews = function () {
        let url = "http://46.21.249.90:1337/api/news/";
        fetch (url)
            .then(response => {
                console.log(response)
                return response.json()
            })
            .then(json => {
                console.log(json);
                let hrf = window.location.href.split("/");
                let last = hrf[hrf.length-1];
                if(last != "all"){
                    this.setState({news: json.filter((el) => el.tag == last)})
                }else{
                    this.setState({news: json})
                }
            })
            .catch((error) => {
                console.error(error);
            });
    };


